@use 'styles/variables' as v;

.todo-edit__text-field {
  font-size: 1rem;
  padding: 2rem;
  width: 100%;
  height: 300px;
  border-radius: 5px;
  resize: none;
  color: v.$text-color;
  border-left: v.$elem-border-left;
  border-right: v.$elem-border-right;
  border-bottom: v.$elem-border-bottom;
}
